.arrow{
    padding-left: 50px;
    font-weight: bold;
    color: var(--azulFuerte);
    
}
.contenedorPagos {
    width: 70%;
    margin: 1rem auto 0 auto;
    display: grid;
    grid-template-columns: 1fr 2fr;
}

@media only screen and (max-width: 768px) {
    .contenedorPagos {
        width: 90%;
        grid-template-columns: 1fr;
    }
}

.infoPersonal .imagen {
    display: flex;
    align-items: center;
    justify-content: center;
}

.infoPersonal img {
    width: 9rem;
    height: 9rem;
    border-radius: 10%;
}

.infoPersonal .datos {
    width: 95%;
    margin-top: 1rem;
    padding: 13px 20px;
    background: linear-gradient(45deg,var(--azulFuerte),var(--azulClaro) 360%);
    border-radius: 14px;
    color: white;
}

.infoEconomica .datosMovil {
    display: none;
}

@media only screen and (max-width: 768px) {
    .infoPersonal .datos {
        display: none;
    }

    .infoEconomica .datosMovil {
        display: block;
        width: 100%;
        margin-top: 1rem;
        padding: 10px 20px;
        background: linear-gradient(45deg,var(--azulFuerte),var(--azulClaro) 360%);
        border-radius: 7px;
        color: white;
    }
}

.infoPersonal .datos p, .infoEconomica .datosMovil p {
    width: 100%;
    justify-content: space-between;
}

.infoPersonal .datos p label:last-of-type, .infoEconomica .datosMovil p:last-ch {
    font-weight: bold;
}

.infoEconomica {
    padding: 5px;
    width: 100%;
    height: 50vh;
    overflow-y: scroll;
}

.infoEconomica::-webkit-scrollbar{
    width: 5px;
    border-radius: 5px;
}
