form::-webkit-scrollbar-thumb {
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    -webkit-animation: gradient 15s ease infinite;
    animation: gradient 15s ease infinite;
    border-radius: 5px;
} */

.infoEconomica::-webkit-scrollbar-thumb {
    background: rgb(225,254,255);
    background: linear-gradient(180deg, rgba(225,254,255,1) 0%, var(--azulFuerte) 54%, rgba(225,254,255,1) 100%);
    border-radius: 5px;
    
}
.contenedorPagos .infoEconomica .menu {
    width: 100%;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

@media only screen and (max-width: 768px) {
    .infoEconomica .menu {
        padding: 0;
        margin-top: 5px;
        justify-content: space-between;
    }
}

.infoEconomica .menu button {
    background-color: var(--azulFuerte);
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 7px;
    color: white;
    align-items: center;
    justify-content: center;
}

.infoEconomica .menu input {
    border: none;
    width: 75%;
    border-bottom: solid 3px var(--azulClaro);
    background-color: white;
}

.infoEconomica .menu button:last-child {
    background-color: white;
}

.contenedorPagos .infoEconomica .tabla {
    
    border-radius: 7px 7px 0 0;
    margin: 0;
    margin-top: 10px;
    width: 100%;
}

.contenedorPagos .infoEconomica .tabla p,
.infoEconomica .tabla a {
    padding: 10px 0 0 10px;
    margin: 0;
    color: teal;
    font-weight: bolder;
}

.contenedorPagos .infoEconomica .tabla table {
    max-height: 30vh;
    width: calc(80vw - 35vw);
    overflow-x: scroll;
    margin: 7px 5px;
    display: inline-block;
    overflow-y: scroll;
    border-radius: 17px;
}
input{
    outline: none;
    padding: 12px;
    border-radius: 10px;
}
input:hover{
    color: #070707;
    background-color: var(--azulClaro);
}

.contenedorPagos .infoEconomica .tabla table tr:last-of-type {
    border-bottom: solid 2px var(--azulClaro);
}

.contenedorPagos .infoEconomica .tabla table tr th {
    text-align: start;
    color: #f4f5f8;
    padding: 1rem;
    background: var(--azulFuerte);
}

.contenedorPagos .infoEconomica .tabla table tr {
   
}

@media only screen and (max-width: 768px) {
    .contenedorPagos .infoEconomica .tabla table {
        width: calc(90vw - 15px);
        overflow-x: scroll;
        display: inline-block;
    }

    .contenedorPagos .infoEconomica .tabla table tr {
        width: 768px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;

    }
}

.contenedorPagos .infoEconomica .tabla table::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    border-radius: 5px;
}

