} */

.contenedorPagos .infoEconomica .tabla table::-webkit-scrollbar-thumb {
    background: rgb(225, 254, 255);
    background: linear-gradient(180deg, rgba(225, 254, 255, 1) 0%, var(--azulFuerte) 54%, rgba(225, 254, 255, 1) 100%);
    border-radius: 5px;

}

.contenedorPagos input {
    width: 100%;
    border: none;
    text-align: center;
}

.btn-guardar {
    padding: 5px;
    background-color: var(--azulFuerte);
}

.btn-cancelar {
    padding: 5px;
    background-color: crimson;
}
.button-native{
    background-color: var(--azulFuerte);
}